import {ActionsType} from './redux-store'


export type UserType = {
    id: number
    followed: boolean
    photos: {
        small: string|null
        large: string|null
    }
    name: string
    status: string
}

export type UserPageType = {
    users: UserType[]
}

let initialState: UserPageType = {
    users: [
        {
            id: 1,
            status: 'I am a tasya\'s kent, and childhood best friend)',
            followed: false,
            name: 'Rakhat Sovet',
            photos: {
                small: null,
                large: null
            }
        },
        {
            id: 2,
            status: 'I am a tasya\'s kent, and childhood best friend)',
            followed: false,
            name: 'Ermekov Sanjar',
            photos: {
                small: null,
                large: null
            }
        },
        {
            id: 3,
            status: 'I am a tasya\'s kent, and childhood best friend)',
            followed: false,
            name: 'Bekzat Sultan',
            photos: {
                small: null,
                large: null
            }
        },
        {
            id: 4,
            status: 'I am a tasya\'s kent, and childhood best friend)',
            followed: false,
            name: 'Baimusin Danial',
            photos: {
                small: null,
                large: null
            }
        },
    ]
}

export const userReducer = (state: UserPageType = initialState, action: ActionsType) => {
    switch (action.type) {
        case 'TOGGLE-FOLLOW':
            let curr = state.users.find(u => u.id === action.userID)
            if (curr) {
                curr.followed = !curr.followed
            }
            return {
                ...state,
            users: [...state.users]
            }
        case 'SET-USERS':
            return {
                ...state,
                users: [...state.users, ...action.users]
            }

        default:
            return state
    }


}
export let toggleFollowAC = (userID: number) => {
    return {
        type: 'TOGGLE-FOLLOW',
        userID: userID,
    } as const
}
export let setUsersAC = (users: UserType[]) => {
    return {
        type: 'SET-USERS',
        users: users
    } as const
}
